import { Component, Input, NgModule, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { IntercomService } from '@core/services/intercom.service';
import { ViewportService } from '@core/services/viewport.service';
import { DestroyService } from '@shared/services/destroy.service';
import { NgbOffcanvas } from '@ng-bootstrap/ng-bootstrap';
import { HeaderSidenavComponent } from '@core/components/header/components/header-sidenav/header-sidenav.component';
import { OFFCANVAS_HEADER_SIDENAV } from '@core/constants/offcanvas.constant';
import { Location } from '@angular/common';
import { filter } from 'rxjs';

const BOTTOMBAR_HIDDEN_ROUTES = ['meeting-schedule/create', 'meeting-schedule/edit', 'enquiry-detail', 'ad-detail', 'matching-tenants'];
@Component({
    selector: 'findqo-agent-hub-header',
    templateUrl: './agent-hub-header.component.html',
    styleUrls: ['./agent-hub-header.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class AgentHubHeaderComponent implements OnInit {
	constructor(
		public viewport: ViewportService,
		private intercomService: IntercomService,
		private ngbOffCanvas: NgbOffcanvas,
		private router: Router
	) {}

	readonly appRoute = APP_ROUTE;
	activeRoute = '';
	isAgentBottomNavbarHidden = false;

	onOpenHeaderSidenav(): void {
		this.intercomService.toggelIntercomPopup(true);
		this.ngbOffCanvas.open(HeaderSidenavComponent, OFFCANVAS_HEADER_SIDENAV);
	}
	getTabSelected(url: string): void {
		this.isAgentBottomNavbarHidden = BOTTOMBAR_HIDDEN_ROUTES.some((route) => {
			return url.includes(route);
		});

		this.activeRoute = url;
	}

	ngOnInit(): void {
		this.getTabSelected(this.router.url);
		this.router.events.pipe(filter((event) => event instanceof NavigationEnd)).subscribe((event: NavigationEnd) => {
			this.getTabSelected(event.urlAfterRedirects);
		});
	}
}
