import { Platform } from '@angular/cdk/platform';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HeaderFacade } from '@core/components/header/header.facade';
import { APP_ROUTE } from '@core/constants/route.constant';
import { SubscriptionStatus, SubscriptionTier, SubscriptionType } from '@core/enums/profile.enum';
import { SubscriptionInfo } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, combineLatest, takeUntil } from 'rxjs';

enum PromotionMode {
	PROPERTY_VALUATION = 'propertyValuation',
	UPGRADE = 'upgradeSubscription',
}

@Component({
    selector: 'findqo-sticky-promotion-header',
    templateUrl: './sticky-promotion-header.component.html',
    styleUrl: './sticky-promotion-header.component.scss',
    standalone: false
})
export class StickyPromotionHeaderComponent implements OnInit {
	constructor(
		private router: Router,
		public viewPort: ViewportService,
		private myProfileFacade: MyProfileFacade,
		private headerFacade: HeaderFacade,
		private destroy: DestroyService,
		private platform: Platform,
	) {
		this.subscription$ = this.myProfileFacade.getSubscriptionInfo$();
	}

	mode: PromotionMode = PromotionMode.PROPERTY_VALUATION;

	subscription$: Observable<SubscriptionInfo>;

	readonly subscriptionTier = SubscriptionTier;
	readonly subscriptionStatus = SubscriptionStatus;
	readonly subscriptionType = SubscriptionType;

	user$ = this.headerFacade.getUser$();

	subscriptionInfo: SubscriptionInfo;

	isAdDetailsScreen = false;

	bannerContent = {
		propertyValuation: {
			title: {
				desktop: 'Get a FREE Property Valuation in 2-minutes with FindQo.ie Property Valuation Calculator!',
				mobile: 'Get a Free Property Valuation with FindQo.ie Property Valuation Calculator!',
			},
			buttonText: 'Free Valuation',
		},
		upgradeSubscription: {
			title: {
				desktop: 'Upgrade to Premium Unlimited for Only 135/month - Get 10 Seller Leads, Unlimited Property Listings, and More!',
				mobile: 'Upgrade to Premium Unlimited for Only 135/month - Get 10 Seller Leads, Unlimited Property Listings, and More!',
			},
			buttonText: 'Upgrade',
		},
	};

	get isMobile(): boolean {
		return this.platform.ANDROID || this.platform.IOS;
	}

	onNavigate() {
		if (this.mode === 'propertyValuation') {
			this.router.navigate(['/' + APP_ROUTE.propertyWorth]);
		} else {
			this.router.navigate(['/' + APP_ROUTE.agentHub.businessAccount]);

			setTimeout(() => {
				document.getElementById('pricingList')?.scrollIntoView({
					behavior: 'smooth',
					block: 'start',
				});
			}, 100);
		}
	}

	checkSubscription(subscription: SubscriptionInfo): void {
		if (!subscription) {
			this.mode = PromotionMode.PROPERTY_VALUATION;
			return;
		}

		if (
			(subscription?.planTier?.reference !== this.subscriptionTier.PREMIUM_UNLIMITED &&
				subscription?.planTier?.reference !== this.subscriptionTier.PREMIUM_UNLIMITED_YEARLY) ||
			(subscription?.status === this.subscriptionStatus.CANCELLED &&
				(subscription?.planTier?.reference === this.subscriptionTier.PREMIUM_UNLIMITED ||
					subscription?.planTier?.reference === this.subscriptionTier.PREMIUM_UNLIMITED_YEARLY)) ||
			subscription?.subscriptionType === this.subscriptionType.TRIAL ||
			subscription?.status === this.subscriptionStatus.EXPIRED
		) {
			this.mode = PromotionMode.UPGRADE;
		}
	}

	ngOnInit(): void {
		this.user$.pipe(takeUntil(this.destroy)).subscribe((user) => {
			if (user) {
				this.myProfileFacade.loadBusinessProfile();
			}
		});

		combineLatest([this.subscription$.pipe(takeUntil(this.destroy)), this.router.events.pipe(takeUntil(this.destroy))]).subscribe(([subscription, _]) => {
			this.isAdDetailsScreen = this.router.url.includes('property/');

			this.subscriptionInfo = subscription;

			this.checkSubscription(this.subscriptionInfo);
		});
	}
}
