import { Platform } from '@angular/cdk/platform';
import { Component, OnInit } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { DeviceType } from '@core/enums';
import { DestroyService } from '@shared/services/destroy.service';
import { DeviceDetectorService } from 'ngx-device-detector';
import { takeUntil } from 'rxjs';
import { ButtonModule } from '../../../../../shared/components/button/button.module';

@Component({
    selector: 'findqo-download-app-header',
    imports: [MatIconModule, MatDialogModule, ButtonModule, MatButtonModule],
    template: `
		@if (isMobile && isAdDetailsScreen && !isClosed) {
			<div class="d-flex p-2 justify-content-between align-items-center banner">
				<div class="d-flex gap-2 align-items-center">
					<div>
						<button mat-icon-button (click)="onClose()"><mat-icon class="fg-general-3 fs-14px">close</mat-icon></button>
					</div>

					<img class="icon" width="48" height="48" src="/assets/icons/mobile-app-icon.webp" alt="FindQo icon" />

					<div>
						<p class="text-caption-medium fg-general-5">Use the FindQo.ie Mobile App</p>
						<p class="text-caption-2-regular fg-general-4">More Homes, More Features</p>
					</div>
				</div>

				<button mat-flat-button (click)="onClick()" class="gradient-button fg-basic-1 text-caption-regular h-32px">Open</button>
			</div>
		}
	`,
    styles: `
		.banner {
			background-image: url('/assets/images/download-app-banner.webp');
			background-size: cover;
			background-position: center;

			border-bottom: 1px solid var(--general-1);
			border-radius: 4px;
		}
		.icon {
			border-radius: 8px;
		}
		.gradient-button {
			background: linear-gradient(180deg, #ee0088 0%, #ee0088 25%, #ee0088 37.5%, #ee0088 50%, #d20072 75%, #c40066 87.5%, #b6005b 100%);
		}
	`
})
export class DownloadAppHeaderComponent implements OnInit {
	constructor(
		private platform: Platform,
		private router: Router,
		private destroy: DestroyService,
		private device: DeviceDetectorService
	) {}

	isAdDetailsScreen = false;

	get isMobile(): boolean {
		return this.platform.ANDROID || this.platform.IOS;
	}

	isClosed = false;

	onClose(): void {
		this.isClosed = true;
	}

	onClick(): void {
		window.location.href = this.device.device === DeviceType.IPHONE ? APP_ROUTE.external.appStoreApp : APP_ROUTE.external.googlePlayStoreApp;
	}

	ngOnInit(): void {
		this.router.events.pipe(takeUntil(this.destroy)).subscribe(() => {
			this.isAdDetailsScreen = this.router.url.includes('property/');
		});
	}
}
